body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f6f6f6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  background-color: #ffffff;
  width: 100%;
  height: 150px;
  padding-left: 100px;
  padding-right: 100px;
  display: flex;
  flex-direction: row;
}

.header img {
  width: 270px;
  padding-top: 35px;
}

.container {
  margin: 100px;
}

.image {
  width: 300px;
  height: 300px;
  background-size: cover;
  cursor: pointer;
  background-position: center bottom;
}

.image-container {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 20px;
}

.image-container-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  max-width: 1500px;
  flex-wrap: wrap;
  gap: 40px;
  margin: 0 auto;
  cursor: pointer;
}

.search-wrapper {
  display: flex;
  align-items: center;
}

.product-name,
.product-company {
  text-transform: capitalize;
}

.product-price {
  font-weight: bold;
}

.product-madeby {
  font-size: 14px;
}

.pagination-wrapper {
  margin-top: 100px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

button {
  width: 200px;
  height: 40px;
  color: #fff;
  font-weight: 500;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  line-height: 39px;
  padding: 0;
  border: none;
  background-color: #dba39a;
  font-size: 16px;
}

.btn-active span {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}

.btn-active:before,
.btn-active:after {
  position: absolute;
  content: "";
  right: 0;
  bottom: 0;
  background: #dba39a;
  transition: all 0.3s ease;
}

.btn-active:before {
  height: 0%;
  width: 2px;
}

.btn-active:after {
  width: 0%;
  height: 2px;
}

.btn-active:hover:before {
  height: 100%;
}

.btn-active:hover:after {
  width: 100%;
}

.btn-active:hover {
  background: transparent;
}

.btn-active span:hover {
  color: #dba39a;
}

.btn-active span:before,
.btn-active span:after {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  background: #dba39a;
  transition: all 0.3s ease;
}

.btn-active span:before {
  width: 2px;
  height: 0%;
}

.btn-active span:after {
  height: 2px;
  width: 0%;
}

.btn-active span:hover:before {
  height: 100%;
}

.btn-active span:hover:after {
  width: 100%;
}

.btn-1 {
  margin-right: 15px;
}

.btn-2 {
  margin-left: 15px;
}

.button-disabled {
  opacity: 0.5;
}

.select-container {
  display: flex;
  align-items: center;
  margin-left: 50px;
  margin-right: 25px;
  margin-top: 56px;
  height: 40px;
}

.css-t3ipsp-control:hover {
  border-color: #dba39a !important;
  box-shadow: 0 0 0 1px #dba39a;
}

#search-form {
  height: 38px;
  border-radius: 4px;
  border-color: hsl(0, 0%, 70%);
  border-style: solid;
  border-width: 1px;
  width: 400px;
  padding-left: 10px;
  font-size: 16px;
}

input[type="search"]:focus {
  border-color: #dba39a !important;
  outline: 1px solid #dba39a;
  box-shadow: 0 0 0 1px #dba39a;
  opacity: 0.7;
}

img.logo-img {
  width: 20px;
  padding: 0;
}

.search-icon {
  margin-left: -30px;
}

.checkbox-container {
  margin-left: 30px;
  display: flex;
  align-items: center;
}

.label-container {
  display: flex;
  align-items: center;
}

.label-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkbox-container {
  position: relative;
}

/* Create a custom checkbox */
.checkmark {
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  margin-right: 7px;
  background-color: #eee;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* On mouse-over, add a grey background color */
.label-container:hover input ~ .checkmark {
  background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */
.label-container input:checked ~ .checkmark {
  background-color: #dba39a;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  display: none;
}

/* Show the checkmark when checked */
.label-container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.label-container .checkmark:after {
  width: 5px;
  height: 10px;
  margin-top: -3px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.sort-container {
  display: flex;
  align-items: center;
  margin-left: 35px;
  margin-top: 56px;
  height: 40px;
}

.no-matches {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.no-matches-img {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.no-matches-img img {
  width: 100px;
}

.no-matches-p {
  color: #dba39a;
  text-transform: uppercase;
  font-size: 30px;
  font-weight: 700;
}

.reset-button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 25px;
}

.reset-button {
  border-radius: 4px;
}
